  position: relative;
  padding-right: 1.5rem; }

[data-submenu] [data-submenu-item] span {
  position: relative;
  z-index: 2; }

[data-submenu] [data-submenu-item] [data-arrow] {
  transform: translateX(-1rem);
  opacity: 0;
  pointer-events: none; }

[data-submenu] [data-submenu-item]:before {
  content: '';
  display: block;
  width: calc(100% - 1.25rem);
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  background-color: var(--color-black, #000000);
  box-shadow: 4px 4px 0px 0px var(--color-spring-green);
  opacity: 0;
  visibility: hidden;
  transition: all 0s ease-in-out; }

[data-submenu] [data-submenu-item]:after {
  content: '';
  display: block;
  width: 1rem;
  height: 100%;
  position: absolute;
  top: 0;
  left: calc(100% - 1.25rem);
  z-index: 1;
  background-color: var(--color-black, #000000);
  box-shadow: 4px 4px 0px 0px var(--color-spring-green);
  transform: skew(-20deg, 0) translateX(-0.438rem);
  opacity: 0;
  visibility: hidden;
  transition: all 0s ease-in-out; }

